/* App.module.css */

.container {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
  }
  
  .title {
    color: #333;
  }
  
  .form {
    width: 200px;
    margin-top: 20px;
  }
  
  .button,
  .deleteButton {
    background-color: white;
    border: 1px solid gray;
    border-radius: 4px;
    font-size: 15px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover,
  .deleteButton:hover {
    background-color: #4cae4c;
  }
  
.input {
  margin: 20px 0;
}

.filter {
  width: 200px;;
}

.deleteButton {
  margin-left: 10px;
}